<template>
    <div class="authentication__form">
        <!-- signing login section -->
        <div>
            <!-- signing login section -->
            <form
                class="signin-section form-block"
                @submit.prevent="validate"
            >
                <div class="form-block__header">
                    <h2 class="form-block__title">
                        Hi there, captain!
                    </h2>
                    <p class="form-block__description">
                        <router-link
                            class="colored-text"
                            :to="{
                                name: 'register',
                                params: {}
                            }"
                        >
                            Register
                        </router-link>, if you are not a client yet.
                    </p>
                </div>

                <div class="form-block__body">
                    <!-- email -->
                    <form-input
                        v-model="$v.email.$model"
                        :field.sync="email"
                        legend="Email address"
                        prepend-icon="EmailOutlineIcon"
                        type="text"
                        :errors="validationMessage($v.email)"
                        :is-valid="$v.email.$dirty && !$v.email.$anyError"
                        @input.native="$v.email.$touch()"
                        @blur.native="$v.email.$touch()"
                    />
                    <!-- email -->
                    <!-- password -->
                    <form-input
                        v-model="$v.password.$model"
                        :field.sync="password"
                        name="password"
                        legend="Password"
                        prepend-icon="LockIcon"
                        :append-icon="showPassword ? 'EyeIcon' : 'EyeOffIcon'"
                        :type="showPassword ? 'text' : 'password'"
                        :errors="validationMessage($v.password)"
                        :is-valid="$v.password.$dirty && !$v.password.$anyError"
                        @clicked="showPassword = !showPassword"
                        @input.native="$v.password.$touch()"
                        @blur.native="$v.password.$touch()"
                    />
                    <!-- password -->
                    <!-- submit -->
                    <button class="btn-base btn-base_colored btn-base_normal button-width-100">
                        <template v-if="loading">
                            <button-loader />
                        </template>
                        <span v-else>log in</span>
                    </button>
                    <!-- submit -->
                    <!-- remember me -->
                    <div class="form-block__checkbox">
                        <div class="form-block__checkbox--container">
                            <form-checkbox
                                v-model="check"
                                check="login"
                            />
                            <span class="">Remember me</span>
                        </div>
                        <!-- forgot password link -->
                        <router-link
                            class="colored-link"
                            :to="{ name: 'forgot-password', params: {} }"
                        >
                            Forgot password?
                        </router-link>
                        <!-- forgot password link -->
                    </div>
                    <!-- remember me -->
                </div>

                <div class="form-block__footer" />

                <!-- social login -->
                <social-login mode="login" />
                <!-- social login -->
            </form>
            <!-- signing login section -->
        </div>
    <!-- signing login section -->
    </div>
</template>

<script>
import { eventBus } from '@/helpers/event-bus/'

// validation
import { validationMixin } from 'vuelidate';
import formValidationMixin from '@/mixins/formValidationMixin';
import { validationMessage } from 'vuelidate-messages';
import { validationRules } from '@/validation/login/Rules';
import { formMessages } from '@/validation/login/Messages';

import SocialLogin from '@/components/common/SocialLogin.vue';
import ButtonLoader from '@/components/common/ButtonLoader.vue';

import {
    ACTION_LOGIN_CLIENT
} from '@/store/modules/client/action-types';

import { createNamespacedHelpers } from 'vuex';

const { mapActions: mapClientActions } = createNamespacedHelpers('client');

export default {
    name: 'LoginForm',
    components: {
        SocialLogin,
        ButtonLoader
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    data: () => ({
        email: '',
        password: '',
        error: false,
        remember: false,
        showPassword: false,
        message: null,
        loading: false,
        lastPath: null,
        check: false
    }),
    watch: {

    },
    methods: {
        ...mapClientActions([
            ACTION_LOGIN_CLIENT
        ]),
        validationMessage: validationMessage(formMessages),
        async submit() {
            const vm = this;
            try {
                vm.loading = true;
                await this[ACTION_LOGIN_CLIENT]({
                    email: this.email,
                    password: this.password,
                    remember_me: this.check
                })
                this.$emit('loginSuccess')
            } catch (e) {
                console.log(e)
                eventBus.$emit('showSnackBar', e.response.message, 'error')
            } finally {
                vm.loading = false
            }
        }
    }
}
</script>
